import renzheng from '@/static/mbcImg/my/renzheng.png';
import { mapMutations, mapGetters } from 'vuex';
export default {
	name: 'qualificatCert',
	components: {
		
	},
	data() {
		return {
			UserData:[],
			renzheng:renzheng,
		}
	},
	created() {
		
		this.getmy();
	},
	computed: {
		...mapGetters(['GET_MY'])
	},
	watch: {
		GET_MY: {
			handler(a, b) {
				this.UserData = a.Authen; // 认证资质
			},
			deep: true
		}
	},
	filters: {
		ellipsis (value) {
		  if (!value) return ''
		  if (value.length > 15) {
			return value.slice(0,15) + '...'
		  }
		  return value
		},
		ellipsiss (value) {
		  if (!value) return ''
		  if (value.length > 30) {
			return value.slice(0,30) + '...'
		  }
		  return value
		},
		formatDate: function(value) {
			let date = new Date(value);
			let y = date.getFullYear();
			let MM = date.getMonth() + 1;
			MM = MM < 10 ? ('0' + MM) : MM;
			let d = date.getDate();
			d = d < 10 ? ('0' + d) : d;
			let h = date.getHours();
			h = h < 10 ? ('0' + h) : h;
			let m = date.getMinutes();
			m = m < 10 ? ('0' + m) : m;
			let s = date.getSeconds();
			s = s < 10 ? ('0' + s) : s;
			return y + '.' + MM + '.' + d ;
		}
	},
	mounted() {},
	methods: {
		getmy() {
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/compAuth/760', //接口地址。
					// data: this.endParams(params),
					method: 'GET',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						this.list = response.data.content
						this.$store.commit('setAuthen', this.list); // 更新vuex
						// this.$store.commit('setMation', this.List); // 更新vuex
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		gotobianji(){
			console.log('资质认证')
			uni.navigateTo({
				url: '/modules/myCompany/personAndCompany/companyInfor/companyInforEdit'
			});
		},
	}
};
